@import url(https://fonts.googleapis.com/css2?family=Trade+Winds&display=swap);
table {
  visibility: hidden;
}

/* Mobile View */
.header {
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
}

.logo-container {
  font-size: 14px;
}

.logo-container a {
  color: #ee0841;
  text-decoration: none;
}

.nav-options {
  display: flex;
  width: 100%;
  height: 70px;
  position: absolute;
  top: 42px;
  left: -100%;
  opacity: 0;
  transition: all 0.5s ease;
  flex-direction: column;
  list-style-type: none;
  grid-gap: 0;
  background-color: white;
}

.nav-options.active {
  left: 0;
  opacity: 1;
  transition: all 0.5s ease;
  z-index: 1;
  align-content: center;
  padding-left: 0;
}

.menu-icon {
  width: 25px;
  height: 25px;
  cursor: pointer;
  color: #ee0841;
}

.option {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2vh;
  padding: 10px;
}

.sign-up {
  color: #ee0841;
  text-decoration: none;
  padding: 10px 0;
  width: 80%;
  text-align: center;
  align-self: center;
  border-radius: 3px;
  font-weight: bolder;
}

.signin-up {
  display: none;
}

.mobile-menu {
  display: block;
}

.logout-btn {
  border: none;
  outline: none;
  background: transparent;
  color: #ee0841;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  padding: 5px;
  font-family: 'Trade Winds', cursive;
}

.logo-container a:hover,
.menu-icon:hover {
  color: white;
}

