.Container_error__1T6yk {
	border: 1px solid #ee0841;
	text-align: center;
	width: 60%;
	padding: 5px;
	margin: 0 auto;
	color: white;
	box-sizing: border-box;
	font-family: 'Courier New', Courier, monospace;
	animation: Container_FadeAnimation__3BQFh 1s ease-in 2s forwards;
