@keyframes Container_FadeAnimation__3BQFh {
	0% {
		opacity: 1;
		visibility: visible;
		margin: -10px auto;
	}

	50% {
		opacity: 1;
		visibility: visible;
		margin: -20px auto;
	}

	100% {
		opacity: 0;
		visibility: hidden;
		margin: -40px auto;
	}
}

