body {
  background: rgb(229, 218, 218);
  font-family: 'Trade Winds', cursive;
}

.mobile {
  width: 300px;
  height: 600px;
  padding: 0;
  margin: 10px auto;
  position: relative;
}

.mobile > img {
  width: 100%;
  height: 100%;
}

.App {
  width: 265px;
  height: 570px;
  background-image: url(/static/media/bg-1.09fa0726.jpg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  left: 5.9%;
  top: 2.5%;
  box-sizing: border-box;
  overflow: hidden;
  border-radius: 30px;
}

.my-shoe {
  height: 500px;
  overflow-y: scroll;
  box-sizing: border-box;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.shoe-name {
  text-align: center;
  color: #ee0841;
}

.price {
  color: white;
}

.shoe-con {
  height: 150px;
  width: 200px;
  padding: 0;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.shoe-con img {
  width: 100%;
}

.shoe-details {
  color: white;
  width: 90%;
  margin: 10px auto;
  text-align: center;
}

.btns-below {
  display: flex;
  justify-content: space-around;
}

.btns-below a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

.btns-below button {
  border: none;
  outline: none;
  background: none;
  color: white;
  cursor: pointer;
  font-size: 18px;
  font-family: 'Trade Winds', cursive;
}

.btns-below a:hover,
.btns-below button:hover {
  color: #ee0841;
}

.my-shoe::-webkit-scrollbar {
  display: none;
}

